@import url(https://fonts.googleapis.com/css2?family=Capriola&display=swap);
.Header {
  display: flex;
  justify-content: space-between;
}

.Header img {
  width: 2rem;
  padding: 1rem;
}

.Header img:hover {
  animation: pulse 1s;
}

.Header .Header-sections {
  display: flex;
  align-items: center;
  padding: 15px 4px;
}

.Header .Header-sections a {
  text-decoration: none;
  color: inherit;
  margin: 0 6px;
  font-size: 1.1rem;
}

.Header .Header-sections a.active {
  color: #fff498;
  font-size: 1.2rem;
}

/* MEDIA QUERY FOR RESPONSIVE DESIGN */

@media only Screen and (min-width: 250px) and (max-width: 450px) {
  .Header img {
    width: 1.5rem;
  }
  .Header .Header-sections a,
  .Header .Header-sections a.active {
    font-size: 0.9rem;
    margin: 0 3px;
  }
}

.SectionItem {
  width: 12rem;
  height: 12rem;
  background-image: url(data:image/png;base64,);
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  color: black;
  text-decoration: none;
}

.SectionItem:hover {
  animation: tada 2.1s;
}

.SectionItem-infos {
  position: relative;
  top: 4.5rem;
}

.SectionItem-icon {
  font-size: 1.9rem;
  text-shadow: 0px 0px 2px #0e3b4a;
}

.SectionItem-name {
  margin-top: 4px;
}

.Footer {
  width: 100%;
  height: 40px;
  margin-top: 20px;
  display: flex;
  justify-content: center;
}

.Footer a {
  margin: 0 2rem;
  width: 5rem;
}

.Footer a:first-of-type:hover i {
  color: gray;
}
.Footer a:nth-of-type(2):hover i {
  color: #5b93d6;
}

.Footer a i {
  color: white;
  font-size: 1.5rem;
}

.HomePage-title {
  display: flex;
  justify-content: center;
  margin-top: 2rem;
  font-size: 2.5rem;
  color: #fbfcfe;
}

.HomePage-title div {
  text-transform: uppercase;
}

.HomePage-title .iamSection {
  text-align: right;
  float: left;
}
.HomePage-title .slideSection {
  text-align: left;
  overflow: hidden;
  display: block;
  white-space: nowrap;
}
.HomePage-title .slideSection p {
  margin: 0px !important;
}

.HomePage-title .scroller {
  height: 50px;
  line-height: 50px;
  overflow: hidden;
}
.HomePage-title .scroller .inner {
  animation: 10s normal infinite running scroll;
}
@keyframes scroll {
  0% {
    margin-top: 0px;
  }
  16.6% {
    margin-top: 0px;
  }

  33.2% {
    margin-top: -50px;
  }
  49.8% {
    margin-top: -50px;
  }

  66.4% {
    margin-top: -100px;
  }
  83% {
    margin-top: -100px;
  }

  100% {
    margin-top: 0px;
  }
}

.HomePage-bio {
  width: 40%;
  margin: 2rem auto;
  padding: 1rem 0;
  font-size: 1.3rem;
  line-height: 2.2rem;
  background-color: rgb(5 5 14 / 30%);
}

.HomePage .Sections {
  display: flex;
  justify-content: center;
  grid-gap: -5px;
  gap: -5px;
  margin-top: 4rem;
}

.HomePage .Sections .SectionItem:not(:first-child) {
  margin-left: -2rem;
}

.HomePage .Sections .SectionItem:nth-of-type(2),
.HomePage .Sections .SectionItem:nth-of-type(4) {
  margin-top: -3rem;
}

/* MEDIA QUERY FOR RESPONSIVE DESIGN */

@media only Screen and (min-width: 250px) and (max-width: 489px) {
  .HomePage-title {
    font-size: 1.5rem;
  }
  .HomePage-bio {
    font-size: 1.1rem;
  }

  .HomePage .Sections {
    display: flex;
    flex-direction: column;
    margin-top: 7px;
  }

  .SectionItem {
    width: 10rem;
    height: 10rem;
    display: block;
    margin: 0 auto !important;
  }

  .SectionItem-infos {
    top: 3.5rem;
    font-size: 1rem;
  }

  .SectionItem-infos p {
    font-size: 0.9rem;
  }
}

@media only Screen and (min-width: 250px) and (max-width: 670px) {
  .HomePage-title {
    flex-direction: column;
  }
  .HomePage-title .iamSection,
  .HomePage-title .slideSection {
    text-align: center;
  }
  .HomePage-bio {
    width: 90%;
  }
}

.AboutPage h3 {
  margin-top: 0;
}

.AboutPage-info {
  width: 60%;
  padding: 0.5rem 1rem;
  margin: auto;
  background-color: rgb(5 5 14 / 30%);
  font-size: 1.1rem;
  line-height: 1.8rem;
  text-align: left;
  border-radius: 10px;
}

.AboutPage-info a {
  color: #82d8ff;
}

.AboutPage-info a:hover {
  color: whitesmoke;
  text-decoration: none;
}

.social-media-links {
  display: flex;
  justify-content: space-evenly;
  font-size: 1.5rem;
}
.social-media-links a:nth-of-type(1) i {
  color: gray;
}
.social-media-links a:nth-of-type(2) i {
  color: #4267b2;
}
.social-media-links a:nth-of-type(3) i {
  color: #c13584;
}
.social-media-links a:nth-of-type(4) i {
  color: #5b93d6;
}
.social-media-links a:hover i {
  color: whitesmoke;
}

/* MEDIA QUERY FOR RESPONSIVE DESIGN */

@media only Screen and (min-width: 250px) and (max-width: 700px) {
  .Header-sections a {
    font-size: 0.7rem;
  }
  .AboutPage-info {
    width: 95%;
  }
}

.Application {
  position: relative;
  display: inline-block;
  width: 22rem;
  height: 14rem;
  margin: 1rem;
  padding: 2px;
  border: 1px solid #0d3444;
  border-radius: 1rem;
}

.Application img,
.Application-description {
  height: 100%;
  width: 100%;
  border-radius: 1rem;
}

.Application-description {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgb(0 0 0/90%);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.5s, visibility 0.5s;
}

.Application h3 {
  margin-top: 20%;
}

.Application:hover {
  box-shadow: 0 0 7px #fff498;
  transition: 0.5s;
}

.Application:hover .Application-description {
  visibility: visible;
  opacity: 1;
}

.Application-link {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  text-decoration: none;
  z-index: 10;
  /* for IE */
  background-color: white;
  opacity: 0;
  filter: alpha(opacity=0);
}

/* MEDIA QUERY FOR RESPONSIVE DESIGN */

@media only Screen and (min-width: 250px) and (max-width: 450px) {
  .Application {
    width: 16rem;
    height: 9rem;
  }
  .Application-description {
    font-size: 0.9rem;
  }
  .Application h3 {
    margin-top: 10%;
  }
}

.PortfolioPage-content {
  background-color: rgb(5 5 14 / 30%);
  width: 90%;
  margin: auto;
  padding: 1rem;
  border-radius: 5px;
}

.ResumePage h2 {
  margin-top: 0.5rem;
}

.download-button {
  display: block;
  margin: 1rem auto;
  padding: 4px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: 1.3rem;
  background-color: #fff498;
  font-weight: bold;
  color: black;
  text-decoration: none;
}

.download-button i {
  margin-right: 10px;
}

.download-button:hover {
  background-color: transparent;
  color: whitesmoke;
  transition: 0.5s;
}

.ContactPage form {
  display: flex;
  flex-direction: column;
  width: 20rem;
  margin: 2rem auto;
  padding: 1rem;
  background-color: rgb(5 5 14 / 40%);
}

.ContactPage label {
  font-size: 1.2rem;
  margin-bottom: 1.3rem;
}

.ContactPage #subject,
.ContactPage textarea {
  font-family: inherit;
  padding: 3px 10px;
  outline: none;
  border: none;
  background-color: #dbedf3;
  border-radius: 6px;
}

.ContactPage #subject {
  width: 70%;
  height: 1.5rem;
  margin: 0 auto 1rem;
}

.ContactPage textarea {
  height: 10rem;
  margin-bottom: 2rem;
  resize: none;
}

.ContactPage input[type="submit"] {
  margin: auto;
  width: 40%;
  height: 1.7rem;
  font-family: inherit;
  background-color: #fff498;
  font-weight: bold;
  cursor: pointer;
}

.App {
  text-align: center;
  font-family: "Capriola";
}

.page {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.page-content {
  position: relative;
  min-height: calc(100vh - 60px);
}

.fade-appear,
.fade-enter {
  opacity: 0;
  z-index: 1;
}

.fade-appear-active,
.fade-enter.fade-enter-active {
  opacity: 1;
  transition: opacity 300ms linear 150ms;
}

.fade-exit {
  opacity: 1;
}

.fade-exit.fade-exit-active {
  opacity: 0;
  transition: opacity 150ms linear;
}

@keyframes pulse {
  0% {
    transform: scaleX(1) scaleY(1);
  }
  25% {
    transform: scaleX(0.8) scaleY(0.8);
  }
  50% {
    transform: scaleX(1) scaleY(1);
  }
  75% {
    transform: scaleX(0.8) scaleY(0.8);
  }
  100% {
    transform: scaleX(1) scaleY(1);
  }
}

@keyframes tada {
  0% {
    transform: scale(1);
  }

  10%,
  20% {
    transform: scale(0.9) rotate(-10deg);
  }

  30%,
  50%,
  70% {
    transform: scale(1.3) rotate(10deg);
  }

  40%,
  60% {
    transform: scale(1.3) rotate(-10deg);
  }

  80% {
    transform: scale(1) rotate(0);
  }
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: whitesmoke;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body,
html {
  background: url(/portfolio/static/media/galaxy.9e387c85.png) no-repeat center center fixed;
  background-size: cover;
}

