.ContactPage form {
  display: flex;
  flex-direction: column;
  width: 20rem;
  margin: 2rem auto;
  padding: 1rem;
  background-color: rgb(5 5 14 / 40%);
}

.ContactPage label {
  font-size: 1.2rem;
  margin-bottom: 1.3rem;
}

.ContactPage #subject,
.ContactPage textarea {
  font-family: inherit;
  padding: 3px 10px;
  outline: none;
  border: none;
  background-color: #dbedf3;
  border-radius: 6px;
}

.ContactPage #subject {
  width: 70%;
  height: 1.5rem;
  margin: 0 auto 1rem;
}

.ContactPage textarea {
  height: 10rem;
  margin-bottom: 2rem;
  resize: none;
}

.ContactPage input[type="submit"] {
  margin: auto;
  width: 40%;
  height: 1.7rem;
  font-family: inherit;
  background-color: #fff498;
  font-weight: bold;
  cursor: pointer;
}
